import React from 'react';
import Button from '../Forms/Button';
import Input from '../Forms/Input';
import Error from '../Helper/Error'
import useForm from '../../Hooks/useForm';
import useFetch from '../../Hooks/useFetch';
import Head from '../Helper/Head'
import { PASSWORD_LOST } from '../../Api';

const LoginPasswordLost = () => {

  const login = useForm();
  const {data, error, loading, request} = useFetch();

  const locationPag = window.location.href;

  const handleSubmit = async (e)  => {
    e.preventDefault();
    if(login.validate()){
      const {url, options} = PASSWORD_LOST({login:login.value, url:locationPag.replace('perdeu','resetar')});
      await request(url, options)
    }
    
  }

  return (
  <section className="animeLeft">
      <Head title="Perdeu a senha"/>
      <h1 className="title">Perdeu a senha?</h1>
      {data 
      ? <p style={{color:'#4c1'}}>{data}</p> 
      :<form onSubmit={handleSubmit}>
        <Input label="Email / Usu√°rio" type="text" {...login} />
        {loading ? <Button disabled>Enviando...</Button>:<Button >Enviar Email</Button>}
      </form>}
     
      <Error error={error}/>
  </section>
  )
};

export default LoginPasswordLost;
